import React from 'react';
import { useAuth } from '../contexts/AuthContext';

const ProtectedRoute = ({ 
  children, 
  requiredRoles = [], 
  requiredPermissions = [], 
  requireAnyRole = false,
  requireAnyPermission = false 
}) => {
  const { user, hasRole, hasAnyRole, hasPermission, hasAnyPermission } = useAuth();

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-gray-900 mb-4">Authentication Required</h2>
          <p className="text-gray-600">Please log in to access this page.</p>
        </div>
      </div>
    );
  }

  // Check role requirements
  let hasRequiredRole = true;
  if (requiredRoles.length > 0) {
    if (requireAnyRole) {
      hasRequiredRole = hasAnyRole(requiredRoles);
    } else {
      hasRequiredRole = requiredRoles.every(role => hasRole(role));
    }
  }

  // Check permission requirements
  let hasRequiredPermission = true;
  if (requiredPermissions.length > 0) {
    if (requireAnyPermission) {
      hasRequiredPermission = hasAnyPermission(requiredPermissions);
    } else {
      hasRequiredPermission = requiredPermissions.every(permission => hasPermission(permission));
    }
  }

  if (!hasRequiredRole || !hasRequiredPermission) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-red-600 mb-4">Access Denied</h2>
          <p className="text-gray-600 mb-4">
            You don't have permission to access this page.
          </p>
          <div className="text-sm text-gray-500">
            {requiredRoles.length > 0 && (
              <p>Required roles: {requiredRoles.join(', ')}</p>
            )}
            {requiredPermissions.length > 0 && (
              <p>Required permissions: {requiredPermissions.join(', ')}</p>
            )}
            <p>Your roles: {user.roles ? user.roles.join(', ') : 'None'}</p>
          </div>
        </div>
      </div>
    );
  }

  return children;
};

export default ProtectedRoute;
