import React from 'react';
import { createBrowserRouter, RouterProvider, Navigate } from 'react-router-dom';
import { Toaster } from 'react-hot-toast';
import { AuthProvider, useAuth } from './contexts/AuthContext.jsx';
import Login from './components/Login.jsx';
import Register from './components/Register.jsx';
import Dashboard from './components/Dashboard.jsx';
import Leads from './components/Leads.jsx';
import Contacts from './components/Contacts.jsx';
import Accounts from './components/Accounts.jsx';
import Deals from './components/Deals.jsx';
import Activities from './components/Activities.jsx';
import Members from './components/Members.jsx';
import Organizations from './components/Organizations.jsx';
import UserManagement from './components/UserManagement.jsx';
import Layout from './components/Layout.jsx';
import CRMLandingPage from './components/CRMLandingPage.jsx';
import ProtectedRoute from './components/ProtectedRoute.jsx';

function AuthGuard({ children }) {
  const { user, loading } = useAuth();
  
  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen">
        <div className="spinner spinner-lg" />
      </div>
    );
  }
  
  return user ? children : <Navigate to="/home" />;
}

// Create router with future flags to suppress warnings
const router = createBrowserRouter([
  {
    path: "/home",
    element: <CRMLandingPage />
  },
  {
    path: "/login",
    element: <Login />
  },
  {
    path: "/register", 
    element: <Register />
  },
  {
    path: "/",
    element: (
      <AuthGuard>
        <Layout>
          <ProtectedRoute>
            <Dashboard />
          </ProtectedRoute>
        </Layout>
      </AuthGuard>
    )
  },
  {
    path: "/leads",
    element: (
      <AuthGuard>
        <Layout>
          <ProtectedRoute requiredRoles={['Admin', 'Manager', 'Sales', 'Viewer']} requiredPermissions={['LEAD_READ']}>
            <Leads />
          </ProtectedRoute>
        </Layout>
      </AuthGuard>
    )
  },
  {
    path: "/contacts",
    element: (
      <AuthGuard>
        <Layout>
          <ProtectedRoute requiredRoles={['Admin', 'Manager', 'Sales', 'Viewer']} requiredPermissions={['CONTACT_READ']}>
            <Contacts />
          </ProtectedRoute>
        </Layout>
      </AuthGuard>
    )
  },
  {
    path: "/accounts",
    element: (
      <AuthGuard>
        <Layout>
          <ProtectedRoute requiredRoles={['Admin', 'Manager', 'Sales', 'Viewer']} requiredPermissions={['ACCOUNT_READ']}>
            <Accounts />
          </ProtectedRoute>
        </Layout>
      </AuthGuard>
    )
  },
  {
    path: "/deals",
    element: (
      <AuthGuard>
        <Layout>
          <ProtectedRoute requiredRoles={['Admin', 'Manager', 'Sales', 'Viewer']} requiredPermissions={['DEAL_READ']}>
            <Deals />
          </ProtectedRoute>
        </Layout>
      </AuthGuard>
    )
  },
  {
    path: "/activities",
    element: (
      <AuthGuard>
        <Layout>
          <ProtectedRoute requiredRoles={['Admin', 'Manager', 'Support', 'Viewer']} requiredPermissions={['ACTIVITY_READ']}>
            <Activities />
          </ProtectedRoute>
        </Layout>
      </AuthGuard>
    )
  },
  {
    path: "/members",
    element: (
      <AuthGuard>
        <Layout>
          <ProtectedRoute requiredRoles={['Admin']} requiredPermissions={['MEMBER_READ']}>
            <UserManagement />
          </ProtectedRoute>
        </Layout>
      </AuthGuard>
    )
  },
  {
    path: "/organizations",
    element: (
      <AuthGuard>
        <Layout>
          <ProtectedRoute requiredRoles={['Admin']} requiredPermissions={['ORGANIZATION_READ']}>
            <Organizations />
          </ProtectedRoute>
        </Layout>
      </AuthGuard>
    )
  }
], {
  future: {
    v7_startTransition: true,
    v7_relativeSplatPath: true
  }
});

function App() {
  return (
    <AuthProvider>
      <div className="App">
        <Toaster
          position="top-right"
          toastOptions={{
            duration: 4000,
            style: {
              background: '#ffffff',
              color: '#111827',
              border: '1px solid #e5e7eb',
              borderRadius: '12px',
              boxShadow: '0 10px 15px -3px rgba(0, 0, 0, 0.1)',
            },
            success: {
              duration: 3000,
              iconTheme: {
                primary: '#10b981',
                secondary: '#fff',
              },
            },
            error: {
              duration: 4000,
              iconTheme: {
                primary: '#ef4444',
                secondary: '#fff',
              },
            },
          }}
        />
        <RouterProvider router={router} />
      </div>
    </AuthProvider>
  );
}

export default App;
